
.kt-gradient-presets {

  padding: 16px 10px 6px 16px;
  margin-left: -16px;
  margin-right: -16px;
  background: rgba(black, 0.02);
  margin-bottom: 10px;
}

.components-button.kt-preset-button {

}
.components-button.kt-gradient-button {
  background: none;
  box-shadow: none;
  border-color: transparent;
  background-clip: padding-box;
  transition: all .2s;
  border-radius: 100px !important;
  border-width: 2px;
  width: 36px;
  height: 20px;
  margin-right: 6px;
  margin-bottom: 8px;
  outline: none;
  &:hover,&:active,&:focus {
    border-color: rgba(0,0,0,0.6);
    box-shadow: none;
    outline: none;
  }
  &:focus:enabled {
    border-color: rgba(0,0,0,0.5);
    box-shadow: inset 0 0 0 3px rgba(white, .9), 0 0 0 2px rgba(black, 0.5);
  }
}
