.eedee-components-unit-button {
	margin-bottom: 0;

	.components-base-control__field > .eedee-components-unit-button__wrap {
		display: flex;
	}

	.eedee-components-unit-button__slider {
		width: 100%;
		margin-left: 0;
		margin-bottom: 0;
	}

	.eedee-components-unit-button__number {
		padding-left: 4px;
		padding-right: 4px;
		width: 54px;
		margin: 0 2px;
		height: 28px;
	}

	.components-button-group {
		display: flex;
		width: auto;
		margin-left: 3px;
		margin-top: 0;
		padding: 10px 0;

		.eedee-components-unit-button__unit {
			width: auto;
			justify-content: center;
			line-height: 28px;
			height: 30px;
			padding: 0;
			min-width: 30px;
		}
	}
}
